
import { defineComponent } from "vue";
import WebMidi from "webmidi";

export default defineComponent({
  name: "MidiCapability",
  data: () => ({
    loading: true,
    error: false
  }),
  unmounted() {
    WebMidi.disable();
  },
  created() {
    if (!WebMidi.enabled) {
      WebMidi.enable(err => {
        this.error = !!err;
        this.loading = false;
        this.$emit("midiReady");
      });
    }
  },
  emits: ["midiReady"]
});
