
import { defineComponent, PropType } from "vue"; // eslint-disable-line no-unused-vars
import GameResult from "@/types/GameResult"; // eslint-disable-line no-unused-vars

export default defineComponent({
  props: {
    games: {
      required: true,
      type: Array as PropType<GameResult[]>
    }
  },
  computed: {
    virtuosicScore(): number {
      let successes = 0,
        heats = 0;
      this.games.forEach(game => {
        successes += Math.max(
          game.gameInfo.successes - game.gameInfo.mistakes,
          1
        );
        heats += game.gameInfo.heats;
      });
      if (heats === 0) {
        return 0;
      }
      return successes / heats;
    }
  }
});
