
import { defineComponent } from "vue";
import Dropdown from "primevue/dropdown";

export default defineComponent({
  components: {
    Dropdown
  },
  props: {
    modelValue: { required: true },
    label: {
      type: String,
      required: true
    },
    options: {
      type: Array,
      required: true
    },
    uuid: {
      type: Number,
      default: 0
    },
    placeholder: {
      type: String,
      default: ""
    }
  },
  emits: ["update:modelValue"]
});
